<template>
    <div calss="login">
        帳號:<input v-model="username"/>
        密碼:<input type="password" v-model="password"/>
        <button @click="login">登入</button>
    </div>
</template>
<script>
import axios from 'axios'
export default{
    name:'login',
    data(){
        return{
            username:'',
            password:'',
            respdata:[]
        }
    },
    methods:{
        login(){
            axios
            .post("http://127.0.0.1:8081/epay/login",
            this.$qs.stringify({
                user:this.username,
                pwd:this.password
            })
            )
            .then(response=>{
                this.respdata=response.data
                console.log(this.respdata)
                if(this.respdata=="無此帳號"||this.respdata=="密碼錯誤"){
                    alert("帳號密碼錯誤")
                }else{
                    this.$router.push({name:"user",params:{name:this.username,sessionid:this.respdata}})
                }    
                })
            .catch(error=>error)
        },
        fromShow(){
            this.fromshow= !this.fromshow
        }
    }
    
}
</script>