<template>
    <div class="user">
        登錄:{{id}}<br/>
        sessionID:{{sessionid}}
        <br/><br/>
        <button @click="logout">logout</button>
    </div>
</template>
<script>
    export default{
        name:'user',
        data(){
            return{
                id:this.$route.params.name,
                sessionid:this.$route.params.sessionid
            }
        },
        methods:{
            logout(){
                this.id=''
                this.sessionid=''
                this.$router.push("/")
            }
        }
    }
</script>